import React from "react";
import '../Services/Services.css';
import code from '../Services/code.png'
import design from '../Services/design.png'
import app from '../Services/app.png'

const Services = () => {
    return(
        <div className="services">
            <h1 className="head1">We offer expert</h1>
            <h1>Web and Design Services</h1>
            <hr />

            <div className="cards">
                <div className="card">
                    <div><img src={code} alt="" srcset=""  className="card-icon"/></div>
                    <div className="card-head">Web Development</div>
                    <div className="card-text">We Provide Web and CMS development solutions..</div>
                </div>
                <div className="card">
                    <div><img src={design} alt=""  className="card-icon"/></div>
                    <div className="card-head">Designing Services</div>
                    <div className="card-text">For an attractive website, we provide the best design services.</div>
                </div>
                <div className="card">
                    <div><img src={app} alt=""  className="card-icon"/></div>
                    <div className="card-head">Mobile Development</div>
                    <div className="card-text">We are the best mobile app development company.</div>
                </div>
            </div>

            <button className="services-btn">Learn More</button>

        </div>
    )
}

export default Services;