import React from "react";
import Carousel from 'react-bootstrap/Carousel';
import bg1 from '../Project/bg1.avif';
import bg2 from '../Project/bg2.avif';
import bg3 from '../Project/bg3.avif';
import '../Project/Project.css'
import 'bootstrap/dist/css/bootstrap.min.css';
const Project = () =>{
    return(

        <div className="project">
            <h1 className="project-head">
            Some featured projects for real <br /> world applications
            </h1>

            <hr />

            <Carousel className="slide">
      <Carousel.Item className="slide-item">
        <img
          className="d-block w-100"
          src={bg1}
          alt="First slide"
        />
        <Carousel.Caption>
          <h3>First slide label</h3>
          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item className="slide-item">
        <img
          className="d-block w-100"
          src={bg2}
          alt="Second slide"
        />

        <Carousel.Caption>
          <h3>Second slide label</h3>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
        </Carousel.Caption>
      </Carousel.Item >
      <Carousel.Item className="slide-item">
        <img
          className="d-block w-100"
          src={bg3}
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3>Third slide label</h3>
          <p>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur.
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
    </div>
  );
}

export default Project;